import * as React from 'react';

import { Seo } from '@/components/seo';
import { Layout } from '@/components/layout';

import { initializeApp } from 'firebase/app';
import { getFirestore, addDoc, getDocs, collection } from 'firebase/firestore';

const firebaseConfig = {
  apiKey: 'AIzaSyBakeOGclhSzZXdiS_9YKbWmXWcA0h5JiM',
  authDomain: 'kotomusubi-4dcd9.firebaseapp.com',
  projectId: 'kotomusubi-4dcd9',
  storageBucket: 'kotomusubi-4dcd9.appspot.com',
  messagingSenderId: '522547548892',
  appId: '1:522547548892:web:e990b1fccc6ab26e4d1a19',
  measurementId: 'G-DJKG0G4399'
};

const IndexPage = () => {
  const [isPlaying, setIsPlaying] = React.useState(false); // ゲームプレイ中かどうかのフラグ
  const [time, setTime] = React.useState(70);
  const [question, setQuestion] = React.useState('大祓詞を覚えよう！');
  const [rankingData, setRankingData] = React.useState([]);

  React.useEffect(() => {
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.href = '/css/game.css';

    const script1 = document.createElement('script');
    script1.src =
      'https://ajax.googleapis.com/ajax/libs/jquery/3.4.1/jquery.min.js';

    script1.onload = () => {
      onLoad();
    };

    document.head.appendChild(link);
    document.body.appendChild(script1);

    //

    const app = initializeApp(firebaseConfig);
    const db = getFirestore(app);

    const showRankingData = async () => {
      let data = [];

      const docs = await getDocs(collection(db, 'ranking'));

      docs.forEach((doc) => {
        const { name, score } = doc.data();
        data.push({ name, score });
      });

      data = data.sort((a, b) => (a.score < b.score ? 1 : -1)).slice(0, 10);

      setRankingData(data);
    };

    showRankingData();

    const onLoad = () => {
      const $ = window.$;

      //配列に問題文を格納する
      var questionTextList = [
        'たかまがはらに',
        'かむずまります',
        'すめらがむつかむろぎ',
        'かむろみのみこともちて',
        'やほよろづのかみたちを',
        'かむつどへにつどへたまひ',
        'かむはかりにはかりたまひて',
        'あがすめみまのみことは',
        'とよあしはらのみづほのくにを',
        'やすくにとたひらけくしろしめせと',
        'ことよさしまつりき'
      ];

      var config = {
        fps: 60,
        gameWidth: 960, // ゲーム画面横幅(px)
        gameMsec: 70000, // ゲームの制限時間（ミリ秒）
        isComplete: false, // ゲームがコンプリート状態かどうかのフラグ
        currentQuestionIndex: 0, // 表示する問題文のインデックス
        cloudX: 0, // 雲のX軸位置(px)
        stoneX: 0, // 石のX軸位置(px)
        toriiX: 0 // 鳥居のX軸位置(px)
      };

      // 表示するゲームの残り時間（秒）
      var remainTimeSec = Math.floor(config.gameMsec / 1000);

      var remainTimeSecTimer; // ゲーム残り時間表示タイマー
      var animationTimer; // 背景アニメーションのタイマー

      var backgroundLeft = -130;
      var backgroundCloudWidth = 214;
      var backgroundStep = 3;

      // DOM要素 (jQuery)
      var $dogrunImg = $('.dogrun > img');
      var $btnToggle = $('.btn-toggle');
      var $backgroundCloud = $('.background-cloud');
      var $backgroundStone = $('.background-stone');
      var $backgroundTorii = $('.background-torii');
      var $answerForm = $('.answer-form');
      var $answerInput = $('.answer-input');
      var $btnReplay = $('.btn-replay');

      /**
       *
       * 関数定義
       *
       */

      function postRanking() {
        var name = $answerInput.val();

        if (!name) {
          alert('名前を入力してください。');
          return false;
        }

        var result = window.confirm(
          '『' + name + '』でランキング登録しますか？'
        );

        if (!result) return false;

        postRankingData(name, remainTimeSec);
      }

      /**
       * Toggle要素をクリックしたときの挙動
       */
      function toggle() {
        if (config.isComplete) {
          postRanking();
        } else if (!isPlaying) {
          // ゲームを開始する
          resetGame();
          startGame();
        } else {
          // ゲームを辞退する
          if (window.confirm('ゲームを辞退します。よろしいですか？')) {
            resetGame();
          }
        }
      }

      /**
       * カウントダウンをスタートする
       */
      function startCountdown() {
        remainTimeSecTimer = setInterval(function () {
          setTime(--remainTimeSec);
          if (remainTimeSec < 1) {
            clearInterval(remainTimeSecTimer);
            timeoverGame();
          }
        }, 1000);

        setTime(remainTimeSec);
      }

      function resetCountdown() {
        clearInterval(remainTimeSecTimer);
        remainTimeSec = Math.floor(config.gameMsec / 1000);
        setTime(remainTimeSec);
      }

      function moveAnimation() {
        // 問題正解するたびスピードアップさせる変化率
        var boost = config.currentQuestionIndex * 0.5;

        if (config.cloudX < config.gameWidth) {
          config.cloudX += backgroundStep - 2.5 + boost;
          $backgroundCloud.css('left', config.cloudX + 'px');
        } else {
          config.cloudX = backgroundCloudWidth * -1;
        }

        if (config.stoneX < config.gameWidth) {
          config.stoneX += backgroundStep + boost;
          $backgroundStone.css('left', config.stoneX + 'px');
        } else {
          config.stoneX = backgroundLeft; //移動したら、元の位置に戻る
        }

        // if (config.toriiX < config.gameWidth) {
        // 	config.toriiX += backgroundStep - 2 + boost;
        // 	$backgroundTorii.style.left = config.toriiX + 'px';
        // } else {
        // 	config.toriiX = backgroundLeft;  //移動したら、元の位置に戻る
        // }

        animationTimer = setTimeout(moveAnimation, 1000 / config.fps);
      }

      /**
       * アニメーションを停止する
       */
      function stopAnimation() {
        clearTimeout(animationTimer);
      }

      /**
       * 問題欄に文章を表示する
       */
      function displayQuestion() {
        setQuestion(questionTextList[config.currentQuestionIndex]);
      }

      //同一の文章が入力されたとき、次の問題を表示する
      //答えを空欄にする
      //Focus()を実行する
      //送信を中止する
      //1周したら最初の問題を表示させる
      function sendAnswer(e) {
        e.preventDefault();

        if (config.isComplete) {
          postRanking();
          return;
        }

        var currentQuestion = questionTextList[config.currentQuestionIndex];

        if (currentQuestion === $answerInput.val()) {
          // 正解したとき
          config.currentQuestionIndex++;

          if (config.currentQuestionIndex >= questionTextList.length) {
            //すべてクリアできたとき
            completeGame();
            return false;
          }

          displayQuestion();
          $answerInput.focus();
        }

        $answerInput.val('');

        return false;
      }

      /**
       * ゲームをスタートする
       */
      function startGame() {
        setIsPlaying(true);
        $answerForm.removeClass('disable');
        $answerInput.val('').focus();
        displayQuestion();
        startCountdown();
        moveAnimation();
        // 犬が走る
        $dogrunImg[0].src = '/images/dogrun.gif';
        $btnToggle.addClass('btn-toggle-retire').text('辞退');
      }

      /**
       * ゲームをコンプリート
       */
      function completeGame() {
        setIsPlaying(false);
        config.isComplete = true;
        $answerInput.val('');
        clearInterval(remainTimeSecTimer);
        stopAnimation();
        $dogrunImg[0].src = '/images/goal.gif';
        $btnToggle.removeClass('btn-toggle-retire').text('ランキング登録');
      }

      /**
       * ゲーム画面をタイムオーバー状態にする
       */
      function timeoverGame() {
        setIsPlaying(false);
        $answerForm.addClass('disable');
        $answerInput.val('').blur();
        stopAnimation();
        $dogrunImg[0].src = '/images/gameover.gif';
        $btnToggle.removeClass('btn-toggle-retire').text('開始');
      }

      /**
       * ゲーム画面をリセットする
       */
      function resetGame() {
        setIsPlaying(false);
        config.isComplete = false;
        $answerForm.addClass('disable');
        $answerInput.val('').blur();
        config.currentQuestionIndex = 0; // 問題文を最初からにする
        displayQuestion();
        resetCountdown();
        stopAnimation();
        $dogrunImg[0].src = '/images/taiki.png';
        $btnToggle.removeClass('btn-toggle-retire').text('開始');
      }

      /**
       *
       * メイン処理
       *
       */

      async function postRankingData(name, score) {
        await addDoc(collection(db, 'ranking'), {
          name: name,
          score: score
        });

        $answerInput.val('');
        showRankingData();

        $('html, body').animate({
          scrollTop: $('.ranking-title').position().top
        });
      }

      $btnToggle.click(toggle);
      $answerForm.submit(sendAnswer);

      // 『もう一度挑戦する』ボタンを押すと最上部までスクロールする
      $btnReplay.click(function () {
        resetGame();

        $('html, body').animate({
          scrollTop: 0
        });
      });

      $(document).keydown(function (e) {
        // スペースキーでゲームスタート/辞退対応
        if (e.keyCode === 32) {
          if (config.isComplete) return false;
          toggle();
          return false;
        }
      });
    };

    return () => {
      document.head.removeChild(link);
      document.body.removeChild(script1);
    };
  }, []);

  return (
    <Layout>
      <div className='wrapper'>
        <div className='container'>
          <div
            style={{
              position: 'absolute',
              top: 20,
              left: 20,
              color: '#333',
              width: 50,
              height: 50,
              lineHeight: '50px',
              textAlign: 'center',
              fontSize: 30
            }}
          >
            {time}
          </div>

          <div
            style={{
              position: 'relative',
              textAlign: 'center',
              height: 80,
              lineHeight: '80px',
              fontSize: 40,
              userSelect: 'none'
            }}
          >
            {question}
          </div>

          <div className='game-container'>
            <div className='background background-sun'></div>
            <div className='background background-fuji'></div>
            <div className='background background-cloud'></div>
            <div className='background background-stone'></div>
            <div className='background background-torii'></div>

            <div className='dogrun'>
              <img src='/images/taiki.png' />
            </div>

            <div className='runway'></div>
          </div>

          <form
            /*
            action='javascript:void(0);'
            name='myform'
            */
            className='answer-form disable'
          >
            <input
              type='text'
              size='20'
              name='answer'
              className='answer-input'
              autoComplete='off'
            />
            <input type='submit' value='確定' className='btn-enter' />
          </form>

          <button type='button' className='btn-toggle' onClick={() => {}}>
            <p>
              開始
              <br />
              <small>(スペースキー)</small>
            </p>
          </button>
        </div>

        <div className='text-container'>
          <div className='ranking-title'>
            <h1>らんきんぐ</h1>
          </div>
          <div className='rankingNumberOfParticipants'>
            <h2>
              〜こと結び参加者数
              <span className='total-num'>{rankingData.length}</span>人中〜
            </h2>
          </div>

          <table className='ranking-table'>
            <tbody>
              {rankingData.map(({ name, score }, i) => {
                return (
                  <tr
                    key={i}
                    className={`ranking-${
                      [0, 1, 2].includes(i) ? 'top' : ''
                    }Player`}
                  >
                    <td
                      className={
                        i === 0
                          ? 'rank--first'
                          : i === 1
                          ? 'rank--second'
                          : i === 2
                          ? 'rank--third'
                          : ''
                      }
                    >
                      {i + 1}位
                    </td>
                    <td>{name}</td>
                    <td>{score}秒</td>
                  </tr>
                );
              })}
            </tbody>
          </table>

          <div className='ranking-red-line'></div>
          {/* タイムオーバー */}
          {/*
      <table className="ranking-table-timeover">
        <tr className="ranking-Player">
          <td className="timeover">タイムオーバー</td>
          <td><span className="timeover-member">0</span>人</td>
        </tr>
      </table>
      */}
          {/* もう一度ボタン */}
          <button type='button' className='btn-replay'>
            もう一度挑戦する
          </button>
        </div>

        <footer className='footer'>
          <p>
            <small>&copy; 2020 K Shindo</small>
          </p>
        </footer>
      </div>
    </Layout>
  );
};

export default IndexPage;

export const Head = () => {
  return <Seo />;
};
